<template>
  <div>
    <div v-if="!this.empty">
      <v-layout row wrap mt-4 v-for="(item,i) in recentData" :key="i">
        <v-flex xs4 class="px-5">
          <v-img height="200px" v-if="recentData[i].PHOTO" :src="recentData[i].PHOTO"></v-img>
          <v-img height="200px" v-else src="@/assets/news.png"></v-img>
        </v-flex>
        <v-flex xs8>
          <v-card-title style="color:#1A265C" class="pt-0">{{recentData[i].TITLE}}</v-card-title>
          <v-card-text style="font-size:15px">{{recentData[i].CONTENT}}</v-card-text>
        </v-flex>
        <v-flex xs12>
          <v-card-actions>
            <v-flex xs10></v-flex>
            <v-flex xs1>
              <v-icon color="#1A265C" @click="showDeleteDialog(recentData[i])">mdi-delete</v-icon>
            </v-flex>
            <v-flex xs1>
              <v-icon color="#1A265C" @click="showEventDialog(i)">edit</v-icon>
            </v-flex>
          </v-card-actions>
        </v-flex>
        <v-flex xs12 class="mt-5">
          <v-divider></v-divider>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="pb-5 pt-5">
        <v-flex xs12 class="mr-5 text-right">
          <pagination
        :next="next"
        :prev="prev"
        :totalLength="pagination.TOTALPAGES"
        @pageClicked="pageClicked"
      ></pagination>
        </v-flex>
      </v-layout>
    </div>
    <div v-else style="margin-bottom: 30px;
    margin-left: 350px;
    margin-right: 200px;">
      <span class="subtitle-1 mr-5 ml-5">No Events to Show</span>
    </div>
  </div>
</template>

<script>
import moment from "moment";
import pagination from "@/components/material/CommonPagination.vue";
export default {
   components: {
    pagination,
  },
  computed: {
    getEventList: {
      get() {
        return this.$store.getters["adminModule/getEventList"];
      },
      set(data) {
        this.$store.commit("adminModule/setEventList",data);
      }
    },
    showEvent: {
      get() {
        return this.$store.getters["adminModule/getshowEventDialog"];
      },
      set(data) {
        this.$store.commit("adminModule/setShowEvent", data);
      }
    },
    recentData: {
      get() {
        return this.$store.getters["adminModule/getrecentEvents"];
      },
      set(data) {
        this.$store.commit("adminModule/setrecentEvents", data);
      }
    },
    eventListLength() {
      return this.$store.getters["adminModule/getEventList"].length;
    }
  },
  watch: {
    eventListLength() {
      if (this.eventListLength == 0) {
        this.count = 0;
        this.empty = true;
      } else {
        this.empty = false;
        this.count = 1;
      }
    }
  },
  data() {
    return {
     
      pagination: {
        LIMIT: 3,
        OFFSET: 0,
        
      },
      showMore: false,
      empty: false,
      selectedEvent: {},
      showEvents: false,
    };
  },
  destroyed()
  {
      this.$store.commit("closeProgressBar", {});
  },
  methods: {
       pageClicked(data) {
            let lim=(data-1)*3 
            if(this.getEventList.length<=lim)
           {
                  this.getEvents(3,lim);
           }
        else
           {
              this.recentData=this.getEventList.slice(lim,lim+3)
          }
      //this.getNews();
    
    },
    setSelectedEvent(item) {
      this.selectedEvent = item;
      this.showMore = true;
    },
    getEvents(limit, offset) {
       let listlen=this.getEventList.length
      let vm=this
      this.$store
        .dispatch("adminModule/getAllEvent", {
          payload: { limit: limit, offset: offset },
        })
        .then((response) => {
            vm.$store.commit("closeProgressBar", {});
          if (response.data.result.length > 0) {
            this.empty = false;
                 
            for (var i = 0; i < vm.getEventList.length; i++) {
              vm.getEventList[i].DATE = moment
                .unix(vm.getEventList[i].DATE / 1000)
                .format("LL");
            }
            
                  this.recentData=this.getEventList.slice(listlen,listlen+3)
            
            vm.pagination = response.data.pagination;
            vm.pagination = Object.assign( {}, this.someObject, response.data.pagination);
            console.log(vm.pagination)
          } else {
            vm.empty = true;
          }
        }).catch((error) => {
           vm.$store.commit("closeProgressBar", {});
          console.log(error); //Exepection error....
          this.$store.commit("showSnackbar", {
            color: "red",
            duration: 1000,
            message: error,
            heading: "Error",
          });
        });;
    },
    next() {
      this.pagination.LIMIT += 0;
      this.pagination.OFFSET += this.pagination.LIMIT;
       if(this.getEventList<=this.pagination.OFFSET)
           {
              this.getEvents(this.pagination.LIMIT, this.pagination.OFFSET);
           }
        else
           {
              this.recentData=this.getEventList.slice((this.pagination.OFFSET-this.pagination.LIMIT))
          }
     
     // this.pagination.OFFSET += 1;
    },

    prev() {
      this.pagination.LIMIT -= 0;
      this.pagination.OFFSET -=this.pagination.LIMIT;
      if(this.getEventList<=this.pagination.OFFSET)
           {
              this.getEvents(this.pagination.LIMIT, this.pagination.OFFSET);
           }
        else
           {
              this.recentData=this.getEventList.slice((this.pagination.OFFSET-this.pagination.LIMIT))
          }
    //  this.pagination.OFFSET -=1
    },
    showEventDialog(index) {
      this.$store.commit(
        "adminModule/showEventDialog",
        JSON.parse(JSON.stringify(this.getEventList[index]))
      );
    },
    showDeleteDialog(data) {
      this.$store.commit("showDeleteDialog", {
        objectToDelete: data,
        commitToCall: "deleteSelectedEvents",
        deleteActionToDispatch: "deleteEvents"
      });
    }
  },
  beforeMount() {
    this.limit = 10;
    (this.showMore = true), this.$store.commit("showProgressBar", {});
   this.getEvents(3,0)
  }
};
</script>